<template>
  <v-app class="my-background">
    <v-spacer></v-spacer>

    <div class="container">
      <div class="d-flex justify-center">
        <v-card flat color="rgb(0,0,0,0)">
          <div class="d-flex justify-center">
            <v-img
              src="@/assets/logo3.png"
              alt="logo"
              max-width="150"
              max-height="150"
            >
            </v-img>
          </div>
          <v-card-title class="main-text justify-center font-weight-bold"
            > SNS</v-card-title
          >
        </v-card>
      </div>
      <br />
      <v-row justify="center">
        <v-form class="form-data" ref="form" v-model="valid" lazy-validation>
          <!--  -->
          <v-text-field
            clearable
            background-color="white"
            solo
            v-model="credentials.id"
            :counter="20"
            :rules="rules.idRules"
            label=""
            required
          ></v-text-field>

          <!--  -->
          <v-text-field
            clearable
            background-color="white"
            solo
            v-model="credentials.email"
            :rules="rules.emailRules"
            label="E-mail"
            required
          ></v-text-field>

          <!--  -->
          <v-text-field
            clearable
            background-color="white"
            solo
            v-model="credentials.password"
            :rules="rules.passwordRules"
            label=""
            type="password"
            required
          ></v-text-field>

          <!--   -->
          <v-text-field
            clearable
            background-color="white"
            solo
            v-model="credentials.passwordConfirmation"
            :rules="rules.passwordConfirmationRules"
            label=" "
            type="password"
            required
          ></v-text-field>

          <!--  -->
          <v-text-field
            clearable
            background-color="white"
            solo
            v-model="credentials.name"
            :rules="rules.nameRules"
            label=""
            required
          ></v-text-field>

          <!--  -->
          <v-text-field
            clearable
            background-color="white"
            solo
            v-model="credentials.phone"
            :rules="rules.phoneRules"
            label=""
            type="tel"
            required
          ></v-text-field>

          <!--  -->
          <v-text-field
            clearable
            background-color="white"
            solo
            v-model="credentials.bod"
            label=""
            type="date"
          ></v-text-field>
          <v-btn
            rounded
            dark
            large
            :disabled="!valid"
            color="black"
            class="mr-4"
            @click.stop="signup(credentials)"
            width="100%"
          >
            
          </v-btn>
          <v-btn
            text
            dark
            large
            rounded
            class="mr-4 my-2"
            @click.stop="move('Login')"
            width="100%"
          >
            
          </v-btn>
        </v-form>
      </v-row>
    </div>
    <v-spacer></v-spacer>
  </v-app>
</template>

<script>
import axios from "axios";

export default {
  name: "SignUp",
  data: () => ({
    valid: true,
    credentials: {
      id: null,
      email: null,
      password: null,
      passwordConfirmation: null,
      name: null,
      phone: null,
      bod: null,
    },
    rules: {
      idRules: [
        (v) => !!v || "  .",
        (v) =>
          /^[a-zA-Z0-9]*$/.test(v) || " +  .",
        (v) =>
          !(v && v.length > 20) || " 20    .",
      ],

      passwordRules: [
        (v) => !!v || "  .",
        
      ],

      passwordConfirmationRules: [
        (v) =>
          v === this.credentials.password || "  .",
      ],

      nameRules: [
        (v) => !!v || " .",
        (v) => !(v && v.length >= 20) || " 20    .",
      ],

      phoneRules: [(v) => !!v || " "],

      emailRules: [
        (v) => !!v || " .",
        (v) => /.+@.+\..+/.test(v) || "  .",
      ],
    },
  }),

  methods: {
    signup() {
      console.log("userId");
      console.log(this.credentials.id);
      console.log("userName");
      console.log(this.credentials.name);
      let data = new FormData();
      data.append("userId", this.credentials.id);
      data.append("userEmail", this.credentials.email);
      data.append("userPassword", this.credentials.password);
      data.append("userName", this.credentials.name);
      data.append("userPhone", this.credentials.phone);
      data.append("userBod", this.credentials.bod);
      axios({
        method: "POST",
        url: `${process.env.VUE_APP_MCS_URL}/register`,
        headers: {
          "Content-Type": "multipart/form-data",
        },
        data: data,
      })
        .then((res) => {
          console.log(res);
          console.log(res.data);
          this.$router.push("Login");
        })
        .catch((err) => {
          this.$store.commit(
            "snackbar/SET_SNACKBAR",
            "  ."
          );
          console.log(err);
          console.log(err.response);
        });
    },
    validate() {
      this.$refs.form.validate();
    },
    move(name) {
      this.$router.push({ name: name });
    },
  },
};
</script>

<style scoped>
.my-background {
  background-color: rgba(0, 0, 0, 0);
}

.form-data {
  width: 80%;
}

.main-text {
  color: #fff !important;
  font-size: 30px;
}
</style>
